.MeetList {
    padding: 0 12px;
}

.MeetBox {
    background: 'linear-gradient(135deg, #f24973 0%, #3948e6 100%)';
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;

    position: relative;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border-radius: 12px;
    margin-bottom: 15px;
    overflow: hidden;
    color: var(--white);
}
.MeetImg {
    background: 'linear-gradient(135deg, #f24973 0%, #3948e6 100%)';
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;

    position: relative;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
    color: var(--white);
}
.MeetImg::after {
    -webkit-filter: blur(30px);
            filter: blur(30px);
    background: radial-gradient(circle, rgba(0, 0, 0, 0.63) 50%, rgba(255, 255, 255, 0.5) 100%);
    content: "";
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    opacity: .5;
    z-index: 1;
}
.MeetBox::after {
    -webkit-filter: blur(30px);
            filter: blur(30px);
    background: radial-gradient(circle, rgba(0, 0, 0, 0.63) 50%, rgba(255, 255, 255, 0.5) 100%);

    content: "";
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 12px;
    opacity: .5;
    z-index: 1;
}
.MeetBox .MeetName {
    padding: 0;
    font-size: 1.5em;
    z-index: 2;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, .5);
}
.MeetImg .MeetName {
    padding: 0;
    font-size: 1.5em;
    z-index: 2;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, .5);
    word-break: break-all;
    margin: 0 auto;
}
.MeetBox .MeetMore {
    padding: 0;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    z-index: 2;
}
.MeetBox .MeetAction {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0;
    z-index: 2;
}
.MeetBox .MeetAction .MeetMembers {
    flex: 1 1;
}
