.NewMeetBox {
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    border-radius: 12px;
    margin-bottom: 15px;
    overflow: hidden;
    color: var(--white);
}

.NewMeetBox .Placeholder {
    background: transparent;
}

.NewMeetBox .Placeholder .Placeholder__title {
    color: var(--white);
    text-shadow: 1px 1px 2px rgba(0, 0, 0, .9);
}

.NewMeetBox .Placeholder .Placeholder__icon {
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.3);
}

.bigImage{
    height: 120px;
    background-position: center;
    background-size: cover;
    border-radius: 10px 10px 0px 0px;
    border: 1px solid var(--image_border);
    border-bottom: 0;
}

.newsDiv{
    padding: 12px;
    background: var(--pocket_white);
    border-radius: 0px 0px 10px 10px;
    color: var(--pocket_black);
    border: 1px solid var(--image_border);
    border-top: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
}

