.Meeting {
    padding: 5px;
    margin: 5px;
    border-radius: 15px;
}

.Meeting_info {
    display: inline-block;
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 10px;
}

.Meeting_name {
    font-size: 24px;
    text-align: center;
}

.title {
    font-weight: bold;
    font-family: var(--font-tt);
    font-size: 25px;
}

.subtitle {
    font-family: Roboto;
    font-weight: normal;
    font-size: 15px;
    line-height: 1.2;
}

